import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { CardContainer, CardCategory, CardTitle } from "./";
import moment from "moment";
import "./Card.css";

export const Card = ({ blog }) => {
  const [labels, setLabels] = useState([]);
  const history = useHistory();

  const openBlog = (title, number) => {
    history.push(`/${title}/${number}`);
  };

  useEffect(() => {
    const labels = blog.labels.nodes.filter((value) => {
      return value.name !== "blog";
    });

    setLabels(labels);
  }, [blog.labels.nodes]);

  return (
    <CardContainer>
      <div onClick={() => openBlog(blog.title, blog.number)}>
        <>
          {labels.map((value, i) => {
            return <CardCategory value={value} key={i} />;
          })}
        </>
        <CardTitle>{blog.title}</CardTitle>
        <div className="author">
          <span className="authorName">
            <span className="symbol">üñãÔ∏è</span> {blog.author.login}
          </span>
          <span className="blogDate">
            <span className="symbol">üóìÔ∏è</span>{" "}
            {moment(blog.updatedAt).format("DD MMM YYYY")}
          </span>
        </div>
      </div>
    </CardContainer>
  );
};
