import React, { useState, useEffect, useCallback, useRef } from "react";
import moment from "moment";
import Markdown from "markdown-to-jsx";
import readingTime from "reading-time";
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco } from "react-syntax-highlighter/dist/esm/styles/hljs";
import { gql } from "apollo-boost";
import { useQuery } from "@apollo/react-hooks";

import { config } from "../config";
import { Loader } from "../Components/Common";
import { PostContainer, PostTitle, PostDate } from "../Components/Post";
import {
  AuthorDetails,
  AuthorAvatar,
  AuthorName,
} from "../Components/Post/Author";

export default function BlogHome() {
  const issueNumber = parseInt(window.location.href.split("/").pop());
  const GET_POSTS = gql`{
    repository(owner: "${config.githubUserName}", name: "${config.githubRepo}") {
      issue(number: ${issueNumber}) {
        title
        body
        bodyHTML
        number
        author {
          avatarUrl
          login
        }
        updatedAt
      }
    }
  }
  `;
  const [post, setPost] = useState([]);
  const [postNodeId, setPostNodeId] = useState("");

  const { loading, error, data } = useQuery(GET_POSTS);

  const HyperLink = ({ children, ...props }) => (
    <a
      href={props.href}
      target="_blank"
      rel="noopener noreferrer"
      className="blog-post-anchor"
    >
      {children}
      <style jsx="true">
        {`
          a {
            color: #484848;
            font-weight: 400;
          }
        `}
      </style>
    </a>
  );

  const CodeBlock = ({ children }) => (
    <SyntaxHighlighter language="javascript" style={docco}>
      {children.props.children}
    </SyntaxHighlighter>
  );

  useEffect(() => {
    if (!loading) {
      if (data) {
        const issues = data.repository.issue;
        setPostNodeId(issues.id);
        setPost(issues);
      }
    }
  }, [loading, error, data]);

  if (loading) {
    return <Loader />;
  }

  return (
    <>
      {post.title && (
        <PostContainer>
          <PostTitle>{post.title}</PostTitle>
          <div>
            <AuthorDetails>
              <AuthorAvatar
                src={post.author.avatarUrl}
                alt={post.author.login}
              />
              <div>
                <AuthorName>{post.author.login}</AuthorName>
                <PostDate>
                  {moment(post.updatedAt).format("DD MMM YYYY")}
                  {"    "}({Math.round(readingTime(post.body).minutes)} Min
                  Read)
                </PostDate>
              </div>
            </AuthorDetails>
          </div>
          <Markdown
            options={{
              overrides: {
                a: {
                  component: HyperLink,
                },
                pre: {
                  component: CodeBlock,
                },
              },
            }}
          >
            {post.body}
          </Markdown>
        </PostContainer>
      )}
    </>
  );
}
